$brand-color:          #F2EBE2;  // Beige
$brand-color-dark:     #333;     // And 444 for the blog posts
$post-background:      #FFFF;
$post-background-dark: #444;
$highlight:            lighten($brand-color, 35%);
$highlight-dark:       #444;
$text-color:           #0b0404;  // Brownish
$text-color-dark:      #bbb;     // Greyish
$muted-text-color:     #E8BD30;  // My Yellow Brand Color
$headings-font-family: "Playfair Display", Garamond, "Times New Roman", serif;
$body-font-family:     "EB Garamond", Garamond, "Times New Roman", serif;

$body-font-size:       1.3125em; // Good for EB Garamond, not for Playfair Display

$on-bigphone:          375px;
$on-tablet:            768px;
$on-laptop:            960px;
$on-desktop:           1200px;

