body {
  background: $brand-color;
  color: $text-color;
  font-family: $body-font-family;
  font-size: $body-font-size;
  line-height: 1.5;
}

// Added from https://derekkedziora.com/blog/dark-mode-revisited
@media (prefers-color-scheme: dark) {
	body {
    background: $brand-color-dark;
    color: $text-color-dark;
    font-family: $body-font-family;
    font-size: $body-font-size;
    line-height: 1.5;
	}
}

h1 {
  font-size: 3em;
  margin: .5em auto;
  font-family: $headings-font-family;
}

h2 {
  font-size: 2em;
  margin: 1em auto;
  text-align: center;
  font-family: $headings-font-family;
}

h3 {
  font-size: 1.5em;
  // margin: 1px auto;
  margin: 1.3333em auto;
  text-align: center;
  font-family: $headings-font-family;
}

h4 {
  font-size: 1.25em;
  font-style: italic;
  margin: 1.875em auto;
  text-align: center;
  font-family: $headings-font-family;
}

h5 {
  font-size: 1em;
  font-style: italic;
  margin: 2em auto;
  text-align: center;
  font-family: $headings-font-family;
}

h6 {
  font-size: .875em;
  font-style: italic;
  margin: 2.25em auto;
  text-align: center;
  font-family: $headings-font-family;
}

em {
  font-style: italic;
}

strong {
  font-weight: bold;
  font-family: $headings-font-family;
}

a {
  color: $text-color;
}

// Added from https://derekkedziora.com/blog/dark-mode-revisited
@media (prefers-color-scheme: dark) {
  a {
    color: $text-color-dark;
  }
}

a:focus {
  outline: 1px dashed $text-color;
}

// Added from https://derekkedziora.com/blog/dark-mode-revisited
@media (prefers-color-scheme: dark) {
  a:focus {
    outline: 1px dashed $text-color-dark;
  }
}

blockquote {
  margin: 2em auto;
  opacity: .8;
  font-style: italic;
  > * {
    padding: 0 3em;
  }
}

blockquote.epigraph {
  font-style: italic;
}

small {
  font-size: .75em;
  font-family: $headings-font-family;
}

p > cite {
  display: block;
  text-align: right;
}

hr {
  border: 0;
  height: 0;
  @include divider;
  margin: 4em auto; // https://github.com/patdryburgh/hitchens/pull/25/commits/15fe54bae2daa4501e8dd17469462599fa499f34
}

// Added from https://derekkedziora.com/blog/dark-mode-revisited
@media (prefers-color-scheme: dark) {
  hr {
    border: 0;
    height: 0;
    @include divider-darkmode;
    margin: 4em auto;
  }
}

img {
  display: flex;
  max-width: 100%;
  height: auto;
  margin: 2em auto;
}

figure img {
  margin: 2em auto 1em;
}

figcaption {
  font-size: .875em;
  font-style: italic;
  text-align: center;
  margin-bottom: 2em;
  opacity: .7;
  font-family: $headings-font-family;
}

.divided::after {
  content: "";
  @include divider;
}

// Added from https://derekkedziora.com/blog/dark-mode-revisited
@media (prefers-color-scheme: dark) {
  .divided::after {
    content: "";
    @include divider-darkmode;
  }
}

.home {
  max-width: 30em;
  margin: auto;
  padding: 4em 1em;
}

.content-title {
  font-size: 2em;
  margin-bottom: 2em;
  text-align: center;
}

.post-date {
  color: $muted-text-color;
  display: block;
  font-size: .825em;
  white-space: nowrap;
  text-transform: uppercase;
  .post-link & {
    padding: .5em 0;
  }
}

.site-credits {
  margin: 0 auto 2em;
  padding: 0 2em;
  text-align: center;
}

.skip-navigation {
  background: $brand-color;
  border: 1px dashed transparent;
  display: block;
  font-size: .875em;
  font-weight: 700;
  margin-top: -2.625rem;
  padding: .5rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  &:hover,
  &:focus {
    background: lighten($brand-color, 2.5%);
    border-color: $text-color;
    margin-top: 0;
  }
}

// Added on 27-08-2022
p.dropcap::first-letter {
      float: left;
      font-size: 5em;
      margin: 0 .1em 0 0;
      line-height: 0.85;
      // color: $muted-text-color
    }

// Estimated Reading Time
.ert {
  font-size: 1em;
  font-style: italic;
  margin: 2em auto;
  text-align: center;
}
