.footnote {
  border: 1px solid $muted-text-color;
  border-radius: 1em;
  color: $text-color;
  display: inline;
  font-size: .75em;
  font-weight: 700;
  padding: 0 .75em;
  text-decoration: none;
  margin: 0 .25em;
  &:hover,
  &:focus {
    background: $brand-color;
    border-color: $brand-color;
  }
}

.footnotes::before {
  content: '';
  @include divider;
  margin: 4em auto;
}

// Added from https://derekkedziora.com/blog/dark-mode-revisited
@media (prefers-color-scheme: dark) {
  .footnotes::before {
    content: '';
    @include divider-darkmode;
    margin: 4em auto;
  }
}

.footnotes {
  margin-bottom: 4em;
}

.footnotes li {
  margin-bottom: 1em;
}

.reversefootnote {
  font-size: .75em;
  opacity: .75;
}
